
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.gameChartWrap {
  width: 95%;
  margin: auto;
  padding-bottom: 43%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.gameChartWrap .chartTitle {
    padding-bottom: 20px;
}
.gameChartWrap .tableBlock {
    position: relative;
}
.gameChartWrap .tableBlock #canvas {
      z-index: 0;
      position: absolute;
      left: 0;
      top: 0;
}
.gameChartWrap .tableBlock table {
      width: 100%;
      z-index: 999;
      position: absolute;
      left: 0;
      top: 0;
}
.gameChartWrap .tableBlock table thead {
        border-bottom: 2px solid #000;
}
.gameChartWrap .tableBlock table thead tr:first-child th {
          padding: 10px 0px;
}
.gameChartWrap .tableBlock table thead .col {
          border: 1px solid #000;
          color: #000;
          padding: 5px;
}
.gameChartWrap .tableBlock table tbody tr {
        border-bottom: 1px solid #ccc;
        border-style: dotted;
}
.gameChartWrap .tableBlock table tbody tr .col {
          color: #000;
          padding: 10px 0px;
}
.gameChartWrap .tableBlock table tbody tr .winN {
          color: #FFF;
}
